sh')\r\n                    this.$dispatch('show-success')\r\n                } else {\r\n                    this.$dispatch('show-error')\r\n                }\r\n            })\r\n        }\r\n    },\r\n    components: {\r\n        modal,\r\n        vSelect\r\n    },\r\n    vuex: {\r\n        getters: {\r\n            appTypes\r\n        }\r\n    },\r\n    events: {\r\n        'showModify' (param) {\r\n            this.modifyModal = true\r\n\r\n            this.$http({\r\n                url: '/package_edit/?id=' + param,\r\n                method: 'GET'\r\n            })\r\n            .then(response => {\r\n                let dict = response.data\r\n\r\n                this.packName = dict.name\r\n                this.projectName = dict.project\r\n                this.type = dict.type\r\n                this.version = dict.version\r\n                this.remark = dict.remark\r\n                this.id = param\r\n            })\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.add-menu {\r\n    height: 34px;\r\n    line-height: 34px;\r\n    padding: 0;\r\n    color: #009688;\r\n    cursor: pointer;\r\n}\r\n</style>"],"sourceRoot":"webpack://"}]);

